
body{margin:0;padding:0;font-family:'Raleway', sans-serif;font-size:16px;overflow-y:hidden
}
h1,h2,h3,h4{font-family:'Cabin', sans-serif;font-weight:400;line-height:1.25
}
p,a,li,span{font-family:'Raleway', sans-serif;font-weight:400;line-height:1.25
}
#app{min-height:100vh;display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap
}
button{background-color:rgba(1,1,1,0.75);color:white;padding:.25em;margin:.25em;border:unset;font-family:'Cabin', sans-serif;font-weight:400;font-size:1.5em;line-height:1.25;border:rgba(18,75,108,0.1) 5px solid;transition:border 200ms
}
button:hover{background-color:#124B6C;color:white;border:#0d3a54 5px solid;transition:border 200ms
}
@media (max-width: 600px){
body{overflow-y:auto
}
.header{max-height:20vh
}
}



